import { Component, OnInit } from '@angular/core';

@Component({
  selector: 'app-random',
  templateUrl: './random.component.html',
  styleUrls: ['./random.component.scss']
})
export class RandomComponent implements OnInit {
  public selectedUser = '';
  public users = [
    {
      id: 0,
      name: 'Ivan Ivanov',
      src: 'http://lorempixel.com/100/100/city',
      isActive: false
    },
    {
      id: 1,
      name: 'Peter Petrov',
      src: 'http://lorempixel.com/100/100/nature',
      isActive: false
    },
    {
      id: 2,
      name: 'Andrey Andreev',
      src: 'http://lorempixel.com/100/100/abstract',
      isActive: false
    },
    {
      id: 3,
      name: 'Viktor Viktorov',
      src: 'http://lorempixel.com/100/100/cats',
      isActive: false
    },
    {
      id: 4,
      name: 'Dmitry Dmitrov',
      src: 'http://lorempixel.com/100/100/food',
      isActive: false
    },
    {
      id: 5,
      name: 'Alex Alexandrov',
      src: 'http://lorempixel.com/100/100/animals',
      isActive: false
    },
    {
      id: 6,
      name: 'Daniel Danielov',
      src: 'http://lorempixel.com/100/100/business',
      isActive: false
    },
    {
      id: 7,
      name: 'Vlad Vladimirov',
      src: 'http://lorempixel.com/100/100/people',
      isActive: false
    }
  ];
  private selectedId;

  constructor() {}

  ngOnInit() {}

  public start() {
    let randomNumber = this.getRandomInt(0, 8);
    this.reset();
    this.users.forEach(user => {
      if (user.id === randomNumber) {
        if (user.id === this.selectedId) {
          this.start();
        } else {
          user.isActive = true;
          this.selectedId = user.id;
        }
      }
    });
  }

  private reset() {
    this.users.forEach(user => {
      user.isActive = false;
      this.selectedUser = '';
    });
  }

  private getRandomInt(min, max) {
    return Math.floor(Math.random() * (max - min)) + min;
  }

  public runRandom() {
    let timer = setInterval(function() {
      this.start();
    }.bind(this), 200);

    setTimeout(function() {
      clearInterval(timer);
      this.users.forEach(user => {
        if (user.isActive) {
          this.selectedUser = user.name;
        }
      });
    }.bind(this), 2500);
  }
}
